
.wrapper {
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
  display: grid;
  grid-template: 10rem 1fr 3rem / 1fr;
  grid-template-areas: 
      "header"
      "main"
      "footer";
}

.wrapper-message {
  grid-template: 10rem 3rem 1fr 3rem / 1fr;
  grid-template-areas: 
      "header"
      "message"
      "main"
      "footer";
}

.upper-container {
  grid-area: header;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 10rem;
  z-index: 99;
  background-color: #282c34;
  border-bottom: 2px solid lightslategray;
}

.message {
  grid-area: message;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  color: red;
}


.button-get-container {
  width: 50%;
  display: flex;
  margin-left: 2rem;
}

.button-clear-data {
  margin-left: 1rem;
}

main {
  grid-area: main;
  border-radius: 8px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.main-centered {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

footer {
  grid-area: footer;
  border-top: 2px solid lightslategray;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: calc(10px + 1vmin);
}