* {
    margin: 0;
    padding: 0;
}

.container {
    margin-top: 1rem;
}

.card {
    margin: 1rem;
    margin-top: 2rem;
    padding: 0.5rem 1rem;
    border: 1px solid gray;
    border-radius: 4px;
    width: 12rem;
}

.id-title {
    position: relative;
    background-color: lightgreen;
    color: maroon;
    top: -1.5rem;
    text-align: center;
    border-radius: 8px;
}

.info {
    position: relative;
    top: -1rem;
}

.title {
    color: blue;
}

.no-show-font {
    /* color: transparent; */
}