form {
    border: 1px solid lightslategrey;
    border-radius: 8px;
    width: 50%;
    margin: 1rem;
}

.h3-new {
    text-align: center;
    margin: 0.5rem 0;
}
.inputs {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

input {
    margin: 0.5rem;
}

.button-container {
    margin: 1rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

button {
    width: 8rem;
    height: 2rem;
    background-color: lightgray;
    color: maroon;
    font-weight: bold;
    border-radius: 7px;
    border: 1px solid lightslategray;
}

button:hover {
    background-color: green;
    color: yellow;
    border: 1px solid green;
}